@mixin h1 {
  font-family: 'Playfair Display', serif;
  font-size: 84px;
  font-weight: 400;
  line-height: 130%;

  @include tablet {
    font-size: 64px;
  }

  @include iPad {
    font-size: 54px;
  }

  @include mobile {
    font-size: 40px;
  }
}

@mixin h2 {
  font-family: 'Playfair Display', serif;
  font-size: 64px;
  font-weight: 400;
  line-height: 132%;

  // @include iPad {
  //   font-size: 18px;
  // }

  // @include mobile {
  //   font-size: 16px;
  // }
}

@mixin h4 {
  font-size: 24px;
  line-height: 116%;
  font-weight: 400;

  @include iPad {
    font-size: 20px;
  }

  @include mobile {
    font-size: 18px;
  }
}

