/* Скидання стилів */
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Rubik', sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 120%;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: inherit;
	font-size: inherit;
}

a {
  text-decoration: none;
  cursor: pointer;
  transition: all 0.4s ease;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 0;
}

img {
	vertical-align: top;
}
