@mixin button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 32px;
  border-radius: 12px;
  color: #fff;
  background-color: $orange;

  &:hover {
    box-shadow: 1px 4px 16px rgba(255, 119, 87, 0.6);
  }
}

// // width Conteiner {
@mixin widthConteiner {
  @media (min-width: 1024px) and (max-width: 1919px) {
    width: calc(100vw - #{2 * 32 + px});
  }

  @include full {
    width: 1556px;
  }

  @include iPad {
    width: calc(100vw - #{2 * 20 + px});
  }

  @include mobile {
    width: calc(100vw - #{2 * 16 + px});
  }
}

@mixin paddingConteiner {
  @media (min-width: 1024px) and (max-width: 1919px) {
    padding-left: 32px;
    padding: 32px;
  }

  @include full {
    padding-left: auto;
    padding-right: auto;
  }

  @include iPad {
    padding-left: 20px;
    padding-right: 20px;
  }

  @include mobile {
    padding-left: 16px;
    padding-right: 16px;
  }
}
