@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.HomePage {
  position: relative;
  display: flex;
  height: max-content;
  width: 100%;

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 270px;

    @include tablet {
      margin-bottom: 150px;
    }

    @include iPad {
      margin-bottom: 75px;
    }

    @include mobile {
      margin-bottom: 100px;
    }
  }

  &__title {
    @include paddingConteiner;
    margin-top: 270px;
    z-index: 3;

    @include full {
      @include widthConteiner;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }

    @include tablet {
      margin-top: 200px;
    }

    @include iPad {
      margin-top: 214px;
    }

    @include mobile {
      margin-top: 198px;
    }
  }

  &__h1 {
    @include h1;
    color: #fff;
  }

  &__subtitle {
    @include h4;
    color: #fff;
    padding-top: 16px;
  }

  &__bookNow-block {
    display: flex;
    height: max-content;
    margin-top: 170px;
    z-index: 2;

    // @include full {
    //   height: 216px;
    // }

    @include tablet {
      margin-top: 140px;
    }

    @include iPad {
      margin-top: 100px;
      // margin-bottom: 75px;
    }

    @include mobile {
      flex-direction: column;
      // height: max-content;
      width: calc(100% - #{2 * 16 + px});
      margin: 100px 16px;
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 1035px;
    background-color: #fff;
    gap: 40px;

    @include full {
      padding: 74px 0 74px 182px;
      width: 1303px;
    }

    @include onDesktop {
      padding: 75.5px 0 75.5px 32px;
    }

    @include tablet {
      padding: 55.5px 0 55.5px 32px;
      width: 886px;
    }

    @media (min-width: 768px) and (max-width: 1100px) {
      width: 489px;
      padding: 23px 0 23px 32px;
    }

    @include mobile {
      flex-direction: column;
      border-radius: 16px 16px 0 0;
      align-items: flex-start;
      padding: 20px;
      width: 100%;
    }
  }

  &__bookNow-btn {
    display: flex;
    align-items: center;
    height: 100%;
    width: 170px;
    border-radius: 0 20px 20px 0;
    background-color: $orange;
    font-family: "Playfair Display", serif;
    font-size: 36px;
    line-height: 130%;
    color: #fff;
    padding: 0 20px;
    transition: all 0.4s ease;

    &:hover {
      color: $dark;
    }

    @include mobile {
      width: 100%;
      padding: 40px 20px;
      border-radius: 0 0 16px 16px;
    }
  }

  &__label {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 225px;
    height: 62px;
    color: $grey;
    font-size: 16px;
    text-transform: uppercase;
    border-bottom: 1px solid #000;

    &:hover {
      border-color: $orange;
    }

    @include onDesktop {
      width: 195px;
    }

    @include tablet {
      width: 158px;
    }

    @include iPad {
      width: 158px;
    }

    @include mobile {
      width: 100%;

      &:last-child {
        margin-bottom: 60px;
      }
    }
  }

  &__input {
    border: none;
    outline: none;
    font-family: 'Playfair Display', serif;
    color: $dark;
    cursor: pointer;
  }

  &__labelQuantity {
    cursor: pointer;

    // &--active {
    //   border-color: $orange;
    // }
  }

  &__chooseQuantity {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__inputQuantity {
    display: none;
  }

  &__decorQuantity {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 32px;
      width: 32px;
      border: 2px solid white;
      border-radius: 50%;
      background-color: transparent;
    }
  }
