@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.SpecialOffer {
  display: flex;
  flex-direction: column;
  @include widthConteiner;
  @include paddingConteiner;
  margin: 140px auto;

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__titleBlock {
    display: flex;
    flex-direction: column;
    width: max-content;
  }

  &__title {
    position: relative;
    @include h2;
    color: $dark;
    padding-bottom: 20px;
    text-align: right;

    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 241px;
      height: 3px;
      background-color: $orange;
    }
  }

  &__subtitle {
    padding-top: 32px;
    color: $grey;
    font-size: 24px;
    align-self: flex-end;
  }

  &__list {
    display: flex;
    flex: 0 1 calc((100% - 32px * 2)/3);
    gap: 32px;
    justify-content: space-between;
    margin-top: 100px;

    @media (max-width: 1145px) {
      flex-direction: column;
    }
  }

  &__li {
    width: calc((100% - 32px * 2)/3);
    min-width: 340px;

    @media (max-width: 1145px) {
      width: 100%;
    }
  }
}
