@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.Header {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 128px;
  box-sizing: border-box;
  z-index: 5;
  margin: 0;
  transition: all 0.3s ease;
  @include paddingConteiner;

  &--fixed {
    position: fixed;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgb(27, 27, 27 / 10%);
    height: 98px;
    width: 100%;
    @include paddingConteiner;
    border-radius: 0 0 20px 20px;
  }

  @include iPad {
    height: 114px;
  }

  @include mobile {
    height: 98px;
  }

  &__conteiner {
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: max-content;
    @include widthConteiner;

    @include full {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__logo {
    @include logo;
    z-index: 10;
  }

  &__nav {
    display: flex;
    gap: 32px;
    flex-wrap: wrap;
    row-gap: 10px;
    justify-content: center;
    padding: 0 15px;

    @media (max-width: 1023px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      position: fixed;
      left: -16px;
      right: -16px;
      top: 0;
      height: 100vh;
      background-color: $dark;
      padding: 0;
      overflow: hidden;
      gap: 36px;
      padding-top: 140px;
      transform: translateX(-200%);
      transition: all 0.4s ease;

      &--open {
        transform: translateX(0);
      }
    }
  }

  &__link {
    @include linkText;

    &--sign-up {
      &:hover {
        color: $dark;
      }
    }

    &--fixed {
      color: $brown;
    }

    @media (max-width: 1023px) {
      font-size: 24px;
    }
  }

  &__hamburger {
    display: none;

    @media (max-width: 1023px) {
      display: block;
      background: url('../../images/icons/hamburger.svg') no-repeat center;
      background-size: contain;
      width: 37px;
      z-index: 10;

      &:hover {
        background: url('../../images/icons/menu_hover.svg') no-repeat center;
      }
    }

    &--open {
      background: url('../../images/icons/cross.svg') no-repeat center;

      &:hover {
        background: url('../../images/icons/cross.svg') no-repeat center;
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    gap: 36px;
  }

  &__signUp {
    @include button;
  }
}
