.TourBlock {
  display: flex;
  flex-direction: column;
  height: 630px;
  width: 100%;
  z-index: 2;

  &__preview {
    background: rgba(255, 119, 87, 0.1);
    height: 286px;
    width: 100%;
    object-fit: contain;
    border-radius: 26px 26px 0 0;
    z-index: 1;
  }

  &__info {
    display: flex;
    flex-direction: column;
    padding: 40px 24px;
    background-color: #fff8f1;
    border-radius: 0 0 26px 26px ;
  }

  &__title {
    width: max-content;
    font-family: 'Mulish', sans-serif;
    font-size: 28px;
    line-height: 125%;
    color: $grey;
    margin-bottom: 8px;
    transition: all 0.4s ease;

    &:hover {
      font-weight: 800;
    }
  }

  &__description {
    padding-top: 24px;
    color: $dark;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
  }

  &__price {
    display: flex;
    align-items: center;
    gap: 8px;
    font-style: 20px;
    line-height: 120%;
    color: $grey;

    &--value {
      color: $orange;
      font-size: 40px;
    }
  }

  &__details {
    @include button;
    font-size: 20px;
    line-height: 120%;
    text-transform: uppercase;
  }

