@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.Swiper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  // @include tablet {
  //   height: 880px;
  // }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 3;
  }

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-size: cover;
    background-position: center;
  }
}

.swiper-pagination {
  right: 32px !important;

  @include full {
    right: 80px !important;
  }

  @include mobile {
    display: none;
  }
}

.swiper-pagination-bullet {
  background-color: white;
  width: 16px;
  height: 16px;
  opacity: 1;
  margin: 12px 0 !important;
  z-index: 3;
}

.swiper-pagination-bullet-active {
  background-color: $orange !important;
  width: 16px;
  height: 16px;
}

.prev,
.next {
  position: absolute;
  font-size: 14px;
  color: $orange;
  z-index: 10;
  right: 32px !important;

  @include full {
    right: 80px !important;
  }

  @include mobile {
    display: none;
  }
}

.prev {
  bottom: 40%;
}

.next {
  bottom: 38%;
}

.swiper-button-disabled {
  color: #fff;
  opacity: 0.7;
}
